import { useState, useEffect } from "react";

import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/swiper.min.css"
import "swiper/components/effect-coverflow/effect-coverflow.min.css"
import "swiper/components/effect-fade/effect-fade.min.css"

import "./styles.css";

import SwiperCore, { EffectCoverflow, EffectFade, Controller } from 'swiper/core';

import Loading from './Loading'
import Footer from './Footer'
import LogoSection from "./LogoSection";
import Navbar from "./Navbar";

import categories from './categories'

SwiperCore.use([EffectCoverflow, EffectFade, Controller]);

export default function App() {

  const [controlledSwiper, setControlledSwiper] = useState(null)
  const [loading, setLoading] = useState(true);
  const [categoryIndex, setCategoryIndex] = useState(0);

  const load = () => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)
    }, 2000)
  }

  useEffect(() => {
    load();
  }, [])

  const handleCategorySelection = (index) => {
    load();
    setTimeout(() => {
      setCategoryIndex(index)
    }, 100)
  }

  return <>
    <Loading loading={loading} />
    <Swiper className="golapi-swiper-fade"
      id="controller"
      onSwiper={setControlledSwiper}
      spaceBetween={30}
      effect={'fade'}
      allowTouchMove={false}>
      {categories[categoryIndex].slides.map((slide, index) => (
        <SwiperSlide key={index}>
          <img alt="slides" src={slide.img} />
        </SwiperSlide>
      ))}
    </Swiper>
    <Navbar {...{ category_titles: categories.map(category => category.title), categoryIndex, handleCategorySelection }} />
    <Swiper className="golapi-swiper-coverflow"
      controller={{ control: controlledSwiper }}
      effect={'coverflow'}
      grabCursor={true}
      centeredSlides={true}
      slidesPerView={'auto'}
      coverflowEffect={{
        "rotate": 0,
        "stretch": 10,
        "depth": 1000,
        "modifier": 1,
        "slideShadows": true
      }}>
      {categories[categoryIndex].slides.map((slide, index) => (
        <SwiperSlide key={index}>
          <img alt="slides" src={slide.img} />
          <p className="golapi-slide-text"><span>ARTIST</span> {slide.artist}</p>
        </SwiperSlide>
      ))}
    </Swiper>
    <LogoSection />
    <Footer />
  </>
}